import { GovernrateService } from './../../../Services/governrate.service';
import { Component, OnInit,ViewChild } from '@angular/core';
import { Platform, IonSlides } from '@ionic/angular';
import { Images } from 'src/environments/environment.prod';

@Component({
  selector: 'app-gov-home',
  templateUrl: './gov-home.page.html',
  styleUrls: ['./gov-home.page.scss'],
})
export class GovHomePage implements OnInit {
  @ViewChild(IonSlides, {static: true}) slider: IonSlides;
  slideOpts = {
    effect: 'flip',
    autoplay: {
    delay: 4000
    }
  };
  @ViewChild('slideWithNav2', { static: false }) slideWithNav2: IonSlides;

  sliderTwo: any;
  slideOptsTwo = {
    initialSlide: 1,
    slidesPerView: 3,
    loop: true,
    centeredSlides: false,
    spaceBetween: 5
  };
  sliderData:any[]=[]
  photoPath:any = Images
  constructor(private govService:GovernrateService) { }
  ionViewWillEnter(){
    this.slider.startAutoplay();
    this.govService.GetGovNews().subscribe(res=>{
      this.sliderData = res
    })
  }
  ngOnInit() {
  }

  public ionViewWillLeave(){  
    this.slider.stopAutoplay();
  }
   slidesDidLoad(slides) {
    this.slider.startAutoplay();
  }

}
